.header {
    height: 70px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
  
    .logo-container {
      height: 100%;
      width: 30px;
      padding: 25px;
      z-index: 10;
    }
  
    .options {
      justify-content: flex-end;
      height: 70px;
      width: 100%;
      display: flex;
      margin-bottom: 25px;
      .option {
        height: 100%;
        width: 30px;
        padding: 25px;
        z-index: 10;
    }
  }
}