.quote-homepage {
        display: flex;
        flex-direction: column;
        align-items: center;
        min-height: 100vh;
        margin: 0;
        padding: 0;
        background-color: deepskyblue;
}

.quote-btn {
        cursor: pointer;
        font-size: 2vw; 
        border-radius: 10px;
        padding: 20px;
    }

.css-button-rounded--black {
  min-width: 130px;
  height: 40px;
  color: #fff;
  padding: 5px 10px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
  outline: none;
  border-radius: 5px;
  border: 2px solid #212529;
  background: #212529;
}
.css-button-rounded--black:hover {
  background: #fff;
  color: #212529
}