  .rolodex {
    text-align: center;
  }
  
  h1 {
    font-family: 'Bigelow Rules';
    font-size: 72px;
    color: #0ccac4;
  }
  
  .rolodex {
    min-height: 100vh;
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #f55ff8;
    background-image: linear-gradient(90deg, #cd38fa 0%, #3fcff3 100%);    
    background-size: 200% 200%;
  
    -webkit-animation: backgroundAnimation 5s ease infinite;
    -moz-animation: backgroundAnimation 5s ease infinite;
    -o-animation: backgroundAnimation 5s ease infinite;
    animation: backgroundAnimation 5s ease infinite;
  }
  
  @-webkit-keyframes backgroundAnimation {
    0%{background-position:0% 74%}
    50%{background-position:100% 27%}
    100%{background-position:0% 74%}
  }
  @-moz-keyframes backgroundAnimation {
    0%{background-position:0% 74%}
    50%{background-position:100% 27%}
    100%{background-position:0% 74%}
  }
  @-o-keyframes backgroundAnimation {
    0%{background-position:0% 74%}
    50%{background-position:100% 27%}
    100%{background-position:0% 74%}
  }
  @keyframes backgroundAnimation {
    0%{background-position:0% 74%}
    50%{background-position:100% 27%}
    100%{background-position:0% 74%}
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  
  .blinking {
    font-family: Bungee inline;
    animation: blinkingText 5s infinite;
    -webkit-text-stroke: 1px black;
     color: white;
     text-shadow:
         3px 3px 0 #000,
       -1px -1px 0 #000,  
        1px -1px 0 #000,
        -1px 1px 0 #000,
         1px 1px 0 #000;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: rgb(21, 2, 105);
  }
  @keyframes blinkingText {
    25%{     color: rgb(252, 31, 204);    }
    50%{    color: rgb(255, 238, 0); }
    75%{    color: rgb(224, 22, 56);  }
    100%{    color: rgb(108, 230, 51);  }
  }