.quote { 
    background-color: black;
    color: white;
    font-size: 2vw; 
    font-weight: bold;
    margin: 0 auto;
    padding: 40px;
    border-radius: 20px;
    width: 50%;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    mix-blend-mode: multiply;
    transition: all 0.6s ease;
}